import axios from 'axios';
import authService from './authService';

export const BASE_URL = 'https://assistant.psy-kokh.online';

// –°—Ç–≤–æ—Ä–µ–Ω–Ω—è –µ–∫–∑–µ–º–ø–ª—è—Ä–∞ axios –∑ –±–∞–∑–æ–≤–∏–º URL
const apiClient = axios.create({
    baseURL: BASE_URL,
});

// Request Interceptor: –î–æ–¥–∞–≤–∞–Ω–Ω—è —Ç–æ–∫–µ–Ω–∞ –¥–æ –∫–æ–∂–Ω–æ–≥–æ –∑–∞–ø–∏—Ç—É
apiClient.interceptors.request.use(
    async (config) => {
        // –ü—Ä–æ–ø—É—Å–∫–∞—î–º–æ –¥–æ–¥–∞–≤–∞–Ω–Ω—è —Ç–æ–∫–µ–Ω–∞ –¥–ª—è –∑–∞–ø–∏—Ç—ñ–≤ –∞–≤—Ç–æ—Ä–∏–∑–∞—Ü—ñ—ó, —â–æ–± —É–Ω–∏–∫–Ω—É—Ç–∏ —Ü–∏–∫–ª—ñ–≤, 
        // —Ö–æ—á–∞ authService –≤–∏–∫–æ—Ä–∏—Å—Ç–æ–≤—É—î –æ–∫—Ä–µ–º–∏–π axios –≤–∏–∫–ª–∏–∫.
        const token = authService.getToken();
        if (token) {
            config.headers['authTelegramToken'] = token;
        }
        return config;
    },
    (error) => {
        return Promise.reject(error);
    }
);

// Response Interceptor: –û–±—Ä–æ–±–∫–∞ 401 —Ç–∞ –∞–≤—Ç–æ–º–∞—Ç–∏—á–Ω–µ –æ–Ω–æ–≤–ª–µ–Ω–Ω—è —Ç–æ–∫–µ–Ω–∞
apiClient.interceptors.response.use(
    (response) => {
        return response;
    },
    async (error) => {
        const originalRequest = error.config;

        if (error.response && (error.response.status === 401 || error.response.status === 403) && !originalRequest._retry) {
            originalRequest._retry = true;
            try {
                // –°–ø—Ä–æ–±—É—î–º–æ –æ—Ç—Ä–∏–º–∞—Ç–∏ –Ω–æ–≤–∏–π —Ç–æ–∫–µ–Ω
                const newToken = await authService.validateAndLogin();
                if (newToken) {
                    // –û–Ω–æ–≤–ª—é—î–º–æ –∑–∞–≥–æ–ª–æ–≤–æ–∫ —ñ –ø–æ–≤—Ç–æ—Ä—é—î–º–æ –∑–∞–ø–∏—Ç
                    originalRequest.headers['authTelegramToken'] = newToken;
                    return apiClient(originalRequest);
                }
            } catch (err) {
                console.error('Session expired, login failed:', err);
                // –¢—É—Ç –º–æ–∂–Ω–∞ –¥–æ–¥–∞—Ç–∏ —Ä–µ–¥—ñ—Ä–µ–∫—Ç –∞–±–æ –ø–æ–≤—ñ–¥–æ–º–ª–µ–Ω–Ω—è –∫–æ—Ä–∏—Å—Ç—É–≤–∞—á—É
            }
        }
        return Promise.reject(error);
    }
);

const apiService = {
    // –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –æ—Ç—Ä–∏–º–∞–Ω–Ω—è –∞–Ω–∞–ª—ñ—Ç–∏—á–Ω–∏—Ö –¥–∞–Ω–∏—Ö –∫–ª—ñ—î–Ω—Ç—ñ–≤ –¥–ª—è –æ—Å–Ω–æ–≤–Ω–æ—ó –≥—Ä–∞—Ñ—ñ–∫–∏
    getGraphicsClient() {
        return apiClient.get(`/api/v1/customer/analytics/main`);
    },

    // –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –≤–∏–≤–æ–¥—É –≤—Å—ñ—Ö —Å–µ—Å—ñ–π –∫–ª—ñ—î–Ω—Ç–∞
    getClientConsultations() {
        return apiClient.get(`/api/v1/customer/consultations`);
    },

    // –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –≤–∏–≤–æ–¥—É –∫–æ–Ω–∫—Ä–µ—Ç–Ω–æ—ó —Å–µ—Å—ñ—ó –∫–ª—ñ—î–Ω—Ç–∞
    getOneClientConsultation(consultationID) {
        return apiClient.get(`/api/v1/customer/consultations/${consultationID}`);
    },

    // –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –≤–∏–≤–æ–¥—É –¥–∞–Ω–∏—Ö –∫–æ—Ä–∏—Å—Ç—É–≤–∞—á–∞
    getAllInformationClient() {
        return apiClient.get(`/api/v1/customer/consultations/client_profile`);
    },

    // –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –æ—Ç—Ä–∏–º–∞–Ω–Ω—è —Ü–∏–∫–ª—É –ø–∞—Ä–∏
    getCycleCouple() {
        return apiClient.get(`/api/v1/customer/customers/couple_cycle`);
    },

    // –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –æ—Ç—Ä–∏–º–∞–Ω–Ω—è –æ—Ü—ñ–Ω–∫–∏ –µ–∫—Å–ø–µ—Ä—Ç–∞
    getExpertAssessment() {
        return apiClient.get(`/api/v1/customer/customers/expert_assessments`);
    },

    // –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –æ—Ç—Ä–∏–º–∞–Ω–Ω—è –¥–∞–π–¥–∂–µ—Å—Ç—É –ø—Å–∏—Ö–æ—Ç–µ—Ä–∞–ø–µ–≤—Ç–∏—á–Ω–∏—Ö –¥—É–º–æ–∫ —Å–µ—Å—ñ—ó
    getDigestPsyMind() {
        return apiClient.get(`/api/v1/customer/customers/digest_opinions`);
    },

    // –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –æ—Ç—Ä–∏–º–∞–Ω–Ω—è EFCT –ø—Ä–æ—Ç–æ–∫–æ–ª—É
    getEfctProtocol() {
        return apiClient.get(`/api/v1/customer/efct/protocol`);
    },

    // –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –æ—Ç—Ä–∏–º–∞–Ω–Ω—è EFCT –Ω–∞–≤–∏—á–æ–∫ (Radar Chart)
    getEfctSkills() {
        return apiClient.get(`/api/v1/customer/efct/skills`);
    },

    // –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –æ—Ç—Ä–∏–º–∞–Ω–Ω—è –æ–ø–∏—Å—É –∫–µ–π—Å—É
    getCaseDescription() {
        return apiClient.get(`/api/v1/customer/case_description`);
    },

    // –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –≥–µ–Ω–µ—Ä–∞—Ü—ñ—ó –¥–æ–∫—É–º–µ–Ω—Ç—ñ–≤ (PDF)
    generateDocument(data) {
        // data: { document_type: 'pdf', origin_type: '...' }
        return apiClient.post(`/api/v1/customer/documents`, data);
    },

    // –û—Ç—Ä–∏–º–∞–Ω–Ω—è —Å–ø–∏—Å–∫—É –ø–æ—Å–∏–ª–∞–Ω—å
    getClientAssets() {
        return apiClient.get(`/api/v1/customer/client_assets`);
    },

    // –°—Ç–≤–æ—Ä–µ–Ω–Ω—è –Ω–æ–≤–æ–≥–æ –ø–æ—Å–∏–ª–∞–Ω–Ω—è
    createClientAsset(data) {
        return apiClient.post(`/api/v1/customer/client_assets`, data);
    },

    // –í–∏–¥–∞–ª–µ–Ω–Ω—è –ø–æ—Å–∏–ª–∞–Ω–Ω—è
    deleteClientAsset(assetID) {
        return apiClient.delete(`/api/v1/customer/client_assets/${assetID}`);
    },

    // –ó–±–µ—Ä–µ–∂–µ–Ω–Ω—è –≤—ñ–¥–ø–æ–≤—ñ–¥–µ–π (status: draft)
    saveCaseAnswers(data) {
        return apiClient.post(`/api/v1/customer/answers`, data);
    },

    // –û–Ω–æ–≤–ª–µ–Ω–Ω—è –≤—ñ–¥–ø–æ–≤—ñ–¥–µ–π (status: filled)
    updateCaseAnswers(data) {
        return apiClient.post(`/api/v1/customer/answers/rewrite`, data);
    },
}

export default apiService;